.mainList {
  margin-left: auto;
  margin-right: auto;
  width: 900px;
  background-color: #fff;
  /* padding: 20px; */
}

.dataEntry {
  padding: 20px;
  height: 90px;
}

.totalCostCents {
  margin-right: 10px;
  width: 150px;
}

.stampValues {
  width: 530px;
}

.maxStamps {
  width: 150px;
  margin-left: 10px;
  margin-right: 10px;
}

.generateSolution {
  margin-top: 10px;
  float: right;
  background-color: cornflowerblue;
}

.divider {
  background-color: cornflowerblue;
  width: 98%;
  height: 2px;
  border-radius: 3px;
  margin-left: auto;
  margin-right: auto;
}

.postageSolution {
  background-color: rgb(152, 184, 243);
  border-radius: 10px;
  width: 400px;
  margin-right: auto;
}

.displayPostageSolution {
  display: flex;
  justify-content: space-around;
  gap: 20px;
  flex-wrap: wrap;
  padding-left: 25px;
}

.stamp {
  width: 60px;
  height: 80px;
  background-color: white;
  font-weight: 700;
  border-radius: 5px;
  display: inline-block;
  margin-left: 15px;
  margin-right: 15px;
  line-height: 80px;
  text-align: center;
}
